		JSM.AddVertexToBody (result, -a, -a, +a);
		JSM.AddVertexToBody (result, -a, -a, -a);

		JSM.AddVertexToBody (result, +b, +d, +c);
		JSM.AddVertexToBody (result, +b, +d, -c);
		JSM.AddVertexToBody (result, +b, -d, +c);
